
.theme-light .x-select-dropdown {
  background-color: #fff;
}
.theme-dark .x-select-dropdown {
  background-color: #383838;
}
.x-select-dropdown {
  width: inherit;
  max-height: 200px;
  overflow: auto;
  margin: 5px 0;
  padding: 5px 0;
  background-color: #fff;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.3);
  position: absolute;
  z-index: 900;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


