
.x-progress-root {
  display: inline-block;
  width: 100%;
  height: 6px;
}
.x-progress-root .progress {
  position: relative;
  height: 100%;
  overflow: hidden;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
}
.x-progress-root .animation-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(255,255,255,0), rgba(255,255,255,0.3), rgba(255,255,255,0.3), rgba(255,255,255,0));
  background: linear-gradient(left, rgba(255,255,255,0), rgba(255,255,255,0.3), rgba(255,255,255,0.3), rgba(255,255,255,0));
  -webkit-animation: play 3s infinite;
          animation: play 3s infinite;
}
.x-progress-root.border-radius {
  border-radius: 100px;
}
.x-progress-root.border-radius .border-radius {
  border-radius: 100px;
}
@-webkit-keyframes play {
from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
@keyframes play {
from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
.theme-light .x-progress-root {
  background: #e4e8f1;
}
.theme-dark .x-progress-root {
  background: #e4e8f1;
}


